@import url(https://fonts.googleapis.com/css2?family=Questrial&family=Shippori+Mincho+B1:wght@400;700&display=swap);
body, html {
  box-sizing: border-box;
  font-family: 'Questrial', Arial, Helvetica, sans-serif;
  margin: 0;
  padding: 0;
}

button, 
input {
  font-family: 'Questrial', Arial, Helvetica, sans-serif;
  outline: none;
}

header {
  width: 100%;
  background-color: #008561;
  color: white;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Shippori Mincho B1', 'serif';
}

nav > button {
  border: 2px solid white;
  padding: 15px;
  background-color: transparent;
  cursor: pointer;
  color: white;
}

/* ====================================Base Elements Start=================================== */

.heading {
  margin-left: 15px;
}

hr {
  height: 1px;
  margin-left: 15px;
  background-color: #15372c;
  border: none;
}

label {
  font-size: 1.25rem;
}

/* ====================================Base Elements End=================================== */

.information span {
  margin-left: 15px;
  display: block;
}

.information button {
  margin-left: 15px;
  margin-top: 10px;
  padding: 15px;
  border: none;
  color: white;
  font-family: 'Questrial', Arial, Helvetica, sans-serif;
  cursor: pointer;
}

.information__inputs {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.information__inputs input {
  padding: 20px;
  border: 1px solid #ccc;
  width: 90%;
  transition: all 0.3s ease-in-out;
  margin-top: 5px;
  font-family: 'Questrial', Arial, Helvetica, sans-serif;
}

.information__inputs input:focus {
  border: 1px solid #15372c;
  background-color: #00856225;
}

.information__inputs .form-control {
  width: 45%;
  margin: 15px;
}

.main-form {
  width: 70%;
  margin: 0 auto;
  color: #15372c;
  max-width: 1440px
}

.main-form__button {
  width: 100%;
  text-align: center;
  padding: 15px;
  font-family: 'Questrial', Arial, Helvetica, sans-serif;
  margin-left: 15px;
  margin-top: 30px;
  margin-bottom: 15px;
  border: none;
  color: white;
  background-color: #008561;
  cursor: pointer;
}

.information__actions__delete-btn {
  background-color: tomato;
}

.information__actions__add-btn {
  background-color: #008561;
}

@media(max-width: 768px) {
  .information__inputs .form-control {
    width: 100%;
  }

  .information button,
  .main-form__button {
    width: 95%;
  }

  .main-form {
    width: 90%;
  }

}





/* 

 primary_color: #15372c;  dark 
 secondary_color: #008561;  light

*/
.preview-container {
  width: 70%;
  margin: 0 auto;
  color: #15372c;
  max-width: 1440px;
}

.preview-container h2,
.preview-container h3,
.preview-container p {
  margin-bottom: 5px;
  margin-top: 5px;
}

.preview-container__header {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
}

.preview-container__header__right {
  -webkit-align-self: flex-start;
          align-self: flex-start;
  text-align: end;
}

.preview-container__skills-container ul {
  display: grid;
  grid-template-columns: repeat(3, 30%);
  padding: 0;
}

.preview-container__skills-container li {
  margin-top: 5px;
  margin-bottom: 5px;
  list-style: none;
}

.preview-container__qualifications-section,
.preview-container__experiences-section {
  margin-bottom: 2rem;
}

@media (max-width: 768px) {
  .preview-container {
    width: 90%;
  }

  .preview-container h2 {
    font-size: 1.55;
  }

  .preview-container p {
    margin-bottom: 0;
    margin-top: 0;
  }
}

.skill-item {
    padding: 10px;
    margin-top: 5px;
    margin-bottom: 5px;
    background-color: #0085623d;
    font-size: 14px;
}

.skill-item__close {
    color: #f02f0d;
}


.showcase {
  margin-left: 15px;
  border-left: 2px solid #15372c;
  border-bottom: 1px solid white;
  padding: 10px;
  background-color: #0085623d;
  width: 90%;
  position: relative;
}

.showcase__close {
  position: absolute;
  right: 5px;
  top: 5px;
  color: #f02f0d;
  cursor: pointer;
}

.showcase__header,
.showcase__para {
  margin-bottom: 5px;
  margin-top: 5px;
}

@media (max-width: 768px) {
  .showcase__header {
    font-size: 18px;
  }

  .showcase__para {
    font-size: 14px;
  }
}

